<template>
  <el-main>
    <div class="filter-container">
      <el-input placeholder="a" style="width: 200px;" class="filter-item"/>
      <!-- <el-select placeholder="s" clearable style="width: 90px" class="filter-item"></el-select> -->
    </div>
    <el-table :data="tableData">
      <el-table-column prop="date" label="日期" width="140"></el-table-column>
      <el-table-column prop="name" label="姓名" width="120"></el-table-column>
      <el-table-column prop="address" label="地址">
        <template slot-scope="scope">
          <el-button size="mini" @click="centerDialogVisible = !centerDialogVisible">{{scope.row}}</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="提示"
      :visible.sync="centerDialogVisible"
      width="30%"
      center
      @open="()=>{openfn(2222)}"
    >
      <span>需要注意的是内容是默认不居中的</span>
      <span slot="footer" class="dialog-footer">
        <!-- <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="centerDialogVisible = false">确 定</el-button>-->
      </span>
    </el-dialog>
  </el-main>
</template>

<script>
export default {
  data() {
    let i = 0;
    const item = {
      date: "2016-05-02",
      name: "王小虎",
      address: "上海市普陀区金沙江路 1518 弄"
    };
    return {
      tableData: Array(20).fill(
        Object.assign(item, {
          name: "王小虎" + ++i
        })
      ),
      centerDialogVisible: false
    };
  }
};
</script>

<style>
</style>



